.chat {
    display: flex;
    flex-direction: column;
    flex: 0.65;
}

.chat__header {
    display: flex;
    padding: 10px;
    align-items: center;
    border-bottom: 1px solid rgb(221, 221, 221);
    height: 9vh;
}

.chat__headerInfo {
    flex: 1;
    padding-left: 20px;
}

.chat__headerInfo>h3 {
    margin-bottom: 3px;
    font-size: 16px;
    color: rgb(56, 56, 56);
    font-weight: 500;
}

.chat__headerInfo>p {
    font-size: 12px;
    color: gray;
}

.chat__body {
    flex: 1;
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: scroll;
}

.chat__message {
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    border-radius: 10px;
    background-color: #fff;
    margin-bottom: 30px;
}

.chat__reciever {
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat__timestamp {
    margin-left: 10px;
    font-size: xx-small;
    color: gray;
}

.chat__name {
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
    color: rgb(82, 82, 82);
}

.chat__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    border-top: 1px solid lightgray;
}

.chat__footer>form {
    flex: 1;
    display: flex;
}

.chat__footer>form>input {
    flex: 1;
    padding: 10px;
    border-radius: 30px;
    border: none;
    outline: 0;
}

.chat__footer>form>button {
    display: none;
}

.chat__footer>.MuSvgIcon-root {
    padding: 10px;
    color: gray;
}